import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import FilterByYearButtons from "components/FilterByYearButtons";
import theme from "theme"
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import { useSelector } from 'react-redux'
import NumbersFormatter from "util/NumbersFormatter"
import Grid from "@material-ui/core/Grid";
import LineChart from "components/LineChart";
import PieChart from "components/PieChart";

const useStyles = makeStyles({
  table: {
    marginTop: "1em",
    marginBottom: 30
  },
  Android: {
    color: theme.palette.platforms.Android
  },
  iOS: {
    color: theme.palette.platforms.iOS
  },
  total: {
    color: theme.palette.platforms.total
  },
  diff: {
    color: theme.palette.primary.main
  },
  months: {
    width: 70,
    padding: 10
  },
  td: {
    padding: 6
  },
  success: {
    color: theme.palette.success.main,
    fontWeight: 700
  },
  warning: {
    color: theme.palette.warning.main,
    fontWeight: 700
  },
  charts: {
    marginBottom: 30
  }
});

export default function ActivityScreen({ data }) {
  const classes = useStyles()
  const dataByYears = useSelector((state) => state.app.dataByYears)
  const year = useSelector((state) => state.app.filterByYear)

  const rows = [];
  const createRow = (month, Android, iOS, total, diff, color) => {
    return { month, Android, iOS, total, diff, color }
  }
  const getMonthDiff = (currentTotalUsers, monthIndex) => {
    let formerAndroidUsers = data.dataByMonths.Android[monthIndex - 1]
    let formerOSUsers = data.dataByMonths.iOS[monthIndex - 1]
    if (monthIndex === 0 && year !== "total" && year > 2019) {
      const prevYearAndroidUsers = dataByYears[year - 1].dataByMonths.Android
      const prevYeariOSUsers = dataByYears[year - 1].dataByMonths.iOS
      formerAndroidUsers = prevYearAndroidUsers[prevYearAndroidUsers.length - 1]
      formerOSUsers = prevYeariOSUsers[prevYeariOSUsers.length - 1]
    }
    return currentTotalUsers - (formerAndroidUsers + formerOSUsers) || '';
  }

  for (let i = 0; i < data.months.length; i++) {
    const month = data.months[i];
    const AndroidUsers = data.dataByMonths.Android[i]
    const iOSUsers = data.dataByMonths.iOS[i]
    const total = AndroidUsers + iOSUsers
    const diff = getMonthDiff(total, i);
    rows.unshift(createRow(month, AndroidUsers, iOSUsers, total, diff, diff > 0 ? "success" : "warning"))
  }

  return (
    <>
      <FilterByYearButtons />
      <TableContainer component={Paper} className={classes.table}>
        <Table size="small" aria-label="activity table">
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell align="center" className={classes.Android}>{theme.icons.Android}</TableCell>
              <TableCell align="center" className={classes.iOS}>{theme.icons.iOS}</TableCell>
              <TableCell align="center" className={classes.total}>{theme.icons.active}</TableCell>
              <TableCell align="center" className={classes.diff}>{<TrendingUpIcon />}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.month}>
                <TableCell component="th" scope="row" className={classes.months}>{row.month}</TableCell>
                <TableCell align="center" className={classes.td}>{NumbersFormatter(row.Android)}</TableCell>
                <TableCell align="center" className={classes.td}>{NumbersFormatter(row.iOS)}</TableCell>
                <TableCell align="center" className={classes.td}>{NumbersFormatter(row.total)}</TableCell>
                <TableCell align="center" className={`${classes.td} ${classes[row.color]}`}>
                  {row.color === "success" && "+"}{NumbersFormatter(row.diff)}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Grid item xs={12} className={classes.charts}>
        <LineChart
          months={data.months}
          dataByMonths={data.dataByMonths}
        />
      </Grid>

      <Grid item xs={12} className={classes.charts}>
        <PieChart dataByPlatforms={[data.Android, data.iOS]} />
      </Grid>
    </>
  );
}
