import React from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import { getDataByYear } from "redux/reducer";
import { useSelector, useDispatch } from "react-redux";
import ServerAlert from "components/ServerAlert";

const useStyles = makeStyles({
  root: {
    display: "flex",
    justifyContent: "center",
    margin: "1em auto 0",
    "& > button": {
      margin: "0 .45em",
    },
  },
});

export default function FilterByYearButtons() {
  const classes = useStyles();
  const years = useSelector((state) => state.app.dataByYears);
  const yearFilter = useSelector((state) => state.app.filterByYear);
  const serverError = useSelector((state) => state.app.serverError);
  const dispatch = useDispatch();
  const filterButtons = Object.keys(years)
    .reverse()
    .map((year, i) => {
      return (
        <Button
          variant={yearFilter === year ? "contained" : "outlined"}
          color="primary"
          key={i}
          onClick={() => dispatch(getDataByYear(year))}
        >
          {year}
        </Button>
      );
    });

  if (serverError) return <ServerAlert />

  return (
    <Grid item xs={12}>
      <div className={classes.root}>
        <Button
          variant={yearFilter === "total" ? "contained" : "outlined"}
          color="primary"
          onClick={() => dispatch(getDataByYear("total"))}
        >
          All
      </Button>
        {filterButtons}
      </div>
    </Grid>
  );
}
