import React from 'react'
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import InfoCard from "components/InfoCard";
import FilterByYearButtons from "components/FilterByYearButtons";
import PieChart from "components/PieChart";
import GroupedBar from "components/GroupedBar";
import LineChart from "components/LineChart";

const useStyles = makeStyles({
  block: {
    marginTop: "1.5em",
  },
  standout: {
    marginTop: "1.5em",
    marginBottom: "1em",
  },
});

export default function DownloadsScreen({ data }) {
  const classes = useStyles();
  return (
    <>
      <Grid item xs={12} className={classes.standout}>
        <InfoCard platform="total" downloads={data.total} />
      </Grid>

      <Grid item xs={12}>
        <InfoCard
          platform="Android"
          downloads={data.Android}
        />
      </Grid>

      <Grid item xs={12}>
        <InfoCard platform="iOS" downloads={data.iOS} />
      </Grid>

      <FilterByYearButtons />

      <Grid item xs={12} className={classes.block}>
        <LineChart
          months={data.months}
          dataByMonths={data.dataByMonths}
        />
      </Grid>

      <Grid item xs={12} className={classes.block}>
        <GroupedBar
          months={data.months}
          dataByMonths={data.dataByMonths}
        />
      </Grid>

      <Grid item xs={12} className={classes.standout}>
        <PieChart dataByPlatforms={[data.Android, data.iOS]} />
      </Grid>
    </>
  )
}
