import { setIncrements, resetIncrements, getDataByYear } from "redux/reducer";
import ObjectsCopier from "util/ObjectsCopier";
import { InitialIncrementsState } from "util/InitialDataState";
import DiffDays from "util/DiffDays";

const middleware = (store) => (next) => (action) => {
  const lastDownloadsUpdateDate = localStorage.getItem("amp_last_downloads") || new Date();
  next(action);

  if (action.type === "app/setData") {
    const type = action.payload.type;
    const increments = ObjectsCopier(InitialIncrementsState);
    const freshData = store.getState().app.data;
    const formerData = JSON.parse(
      localStorage.getItem(`former_${type}_data`)
    );

    if (formerData) {
      for (let platform of Object.keys(increments)) {
        increments[platform] = freshData[platform] - formerData[platform];
      }
      increments.days = DiffDays(lastDownloadsUpdateDate)
    }

    store.dispatch(setIncrements(increments));

    localStorage.setItem(
      `former_${type}_data`,
      JSON.stringify(freshData)
    );

    const filterByYear = store.getState().app.filterByYear;
    if (filterByYear !== "total") {
      store.dispatch(getDataByYear(filterByYear));
    }

    /** reset increments after 5 minutes */
    setTimeout(() => {
      store.dispatch(resetIncrements());
    }, 1000 * 60 * 5);
  }
};

export default middleware;
